<template>
  <v-container fluid>
    <v-layout align-center>
      <v-flex>
        <v-container grid-list-md class="homeData">
          <v-layout row wrap>
            <v-flex xs12 class="notification">
              <v-carousel hide-delimiters interval="3000">
                <v-carousel-item
                  v-for="(notification,i) in notifications"
                  :key="i"
                >
                  <v-jumbotron dark>
                    <v-container>
                      <v-layout align-center>
                        <v-flex>
                          <span class="title pl-5">{{notification}}</span>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-jumbotron>
                </v-carousel-item>
              </v-carousel>
            </v-flex>
            <v-flex xs12 sm12 lg12 xlg12 class="slider">
              <v-carousel>
                <v-carousel-item
                  v-for="(item,i) in items"
                  :key="i"
                  :src="item.src"
                  reverse-transition="fade"
                  transition="fade"
                  hide-controls=true
                >
                </v-carousel-item>
              </v-carousel>
            </v-flex>
            <v-flex xs12 sm12 lg6 xlg6>
              <iframe src="static/widgets/MarketData.html" width="100%" height="280px" frameborder="0" scrolling="no">
              </iframe>
            </v-flex>
            <v-flex xs12 sm12 lg6 xlg6>
              <iframe src="static/widgets/screener.html" width="100%" height="280px" frameborder="0" scrolling="no">
              </iframe>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  data () {
    return {
      items: [
        {
          src: '/static/images/carousel/img1.jpg'
        },
        {
          src: '/static/images/carousel/img2.jpg'
        }
      ],
      notifications: [
        'Online seminar coming soon @ Mumbai',
        'Online seminar coming soon @ Banglore'
      ]
    }
  }
}
</script>


